	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import xueshengxuanke from '@/views/xueshengxuanke/list'
	import xuesheng from '@/views/xuesheng/list'
	import xuanxiukecheng from '@/views/xuanxiukecheng/list'
	import xueshengjiangcheng from '@/views/xueshengjiangcheng/list'
	import xueyuanxinxi from '@/views/xueyuanxinxi/list'
	import banjixinxi from '@/views/banjixinxi/list'
	import jiaoshi from '@/views/jiaoshi/list'
	import xueshengchengji from '@/views/xueshengchengji/list'
	import caiwuxinxi from '@/views/caiwuxinxi/list'
	import kechengxinxi from '@/views/kechengxinxi/list'
	import users from '@/views/users/list'
	import kechengleixing from '@/views/kechengleixing/list'
	import xueshengRegister from '@/views/xuesheng/register'
	import xueshengCenter from '@/views/xuesheng/center'
	import jiaoshiRegister from '@/views/jiaoshi/register'
	import jiaoshiCenter from '@/views/jiaoshi/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/xueshengCenter',
			name: '学生个人中心',
			component: xueshengCenter
		}
		,{
			path: '/jiaoshiCenter',
			name: '教师个人中心',
			component: jiaoshiCenter
		}
		,{
			path: '/xueshengxuanke',
			name: '学生选课',
			component: xueshengxuanke
		}
		,{
			path: '/xuesheng',
			name: '学生',
			component: xuesheng
		}
		,{
			path: '/xuanxiukecheng',
			name: '选修课程',
			component: xuanxiukecheng
		}
		,{
			path: '/xueshengjiangcheng',
			name: '学生奖惩',
			component: xueshengjiangcheng
		}
		,{
			path: '/xueyuanxinxi',
			name: '学院信息',
			component: xueyuanxinxi
		}
		,{
			path: '/banjixinxi',
			name: '班级信息',
			component: banjixinxi
		}
		,{
			path: '/jiaoshi',
			name: '教师',
			component: jiaoshi
		}
		,{
			path: '/xueshengchengji',
			name: '学生成绩',
			component: xueshengchengji
		}
		,{
			path: '/caiwuxinxi',
			name: '财务信息',
			component: caiwuxinxi
		}
		,{
			path: '/kechengxinxi',
			name: '课程信息',
			component: kechengxinxi
		}
		,{
			path: '/users',
			name: '管理员',
			component: users
		}
		,{
			path: '/kechengleixing',
			name: '课程类型',
			component: kechengleixing
		}
		]
	},
	{
		path: '/xueshengRegister',
		name: '学生注册',
		component: xueshengRegister
	},
	{
		path: '/jiaoshiRegister',
		name: '教师注册',
		component: jiaoshiRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
